import { FileHandlerInterface } from './file-handler.interface';
import { DiskFileHandler } from './handler/disk.filehandler';
import { DummyFilehandler } from './handler/dummy.filehandler';
import { SambaFilehandler } from './handler/samba.filehandler';
import { SftpReconnectFilehandler } from './handler/sftp-reconnect.filehandler';
import { SftpFilehandler } from './handler/sftp.filehandler';

export class FileSystemConnector {
  readonly #handlers = {
    dummy: DummyFilehandler,
    disk: DiskFileHandler,
    samba: SambaFilehandler,
    sftp: SftpFilehandler,
    'sftp-reconnect': SftpReconnectFilehandler,
  };

  fromDsn(dsn: string): FileHandlerInterface {
    // eslint-disable-next-line no-restricted-syntax
    for (const handler of Object.values(this.#handlers)) {
      const parsedHandler = handler['fromDsn'](dsn);

      if (parsedHandler) {
        return parsedHandler;
      }
    }

    throw new Error('No matching file system handler found for given DSN.');
  }
}
